@import '../../index.scss';

button{
    padding: 10px;
    text-transform: uppercase;
    color: $dark;
    background-color: $light;
    border: none;

    &:hover{
        box-shadow: 0 0 10px $light;
    }

    &.red{
        color: $light;
        background-color: $secondary;

        &:hover{
            box-shadow: 0 0 10px $secondary;
        }
    }
}