@import "../../index.scss";

.arrowTop {
  width: 2rem;
  height: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  left: 2rem;
  bottom: 2rem;
  background-color: rgba($color: $light, $alpha: 0.3);
  border-radius: 50%;
  transition: 0.3s;
  cursor: pointer;
  z-index: 10;

  &:hover {
    background-color: $primary;

    &::after{
        border-color: $light;
    }
  }

  &::after {
    content: "";
    border: solid $primary;
    border-width: 0 3px 3px 0;
    display: inline-block;
    padding: 3px;
    transform: rotate(-135deg);
    -webkit-transform: rotate(-135deg);
  }
}
