@import "../../index.scss";

nav {
    height: 70px;
    background-color: $dark;
    align-items: center;
    justify-content: space-between;

    img {
        width: 60px;
        height: 60px;
    }

    .menu,
    .close {
        width: 40px;
        display: none;
        fill: $light;
        transition: 0.2s;
        cursor: pointer;

        &:hover {
            fill: $primary;
        }
    }

    .close{
        position: absolute;
        top: 10px;
        right: 10px;
    }

    .tabs {
        display: flex;
        align-items: center;
    }

    a,
    svg {
        transition: 0.2s;
    }

    a {
        margin: 0 10px;

        &:hover {
            color: $primary;
        }
    }

    ul {
        margin: 0 2rem 0 1rem;
        padding: 0;
        list-style: none;
        cursor: pointer;

        & > li {
            position: relative;

            &:hover {
                ul {
                    visibility: visible;
                    opacity: 1;
                }

                svg {
                    fill: $primary;
                }
            }

            svg {
                width: 50px;
                margin: 0 0 0 50px;
                fill: $light;
            }

            ul {
                margin: 0;
                visibility: hidden;
                position: absolute;
                right: 0;
                background-color: $dark;
                border-radius: 2px;
                opacity: 0;
                transition: 0.2s;
                overflow: hidden;

                * {
                    color: $light;
                }

                li {
                    width: max-content;
                    padding: 5px;
                    display: flex;
                    align-items: center;
                    transition: 0.2s;

                    img {
                        width: 25px;
                        height: auto;
                        margin: 0 5px 0 0;
                    }

                    &:hover {
                        background-color: $primary;
                    }
                }
            }
        }
    }

    @media only screen and(max-width: 768px) {
        .menu, .close{
            display: block;
        }

        .tabs {
            width: 320px;
            height: 110vh;
            padding: 100px 20px;
            display: flex;
            align-items: flex-start;
            flex-direction: column;
            position: fixed;
            top: 0;
            right: -320px;
            background-color: $dark;
            transition: 0.2s;

            &.open{
                right: 0;
            }

            a{
                margin: 10px 0;
                font-size: 20px;
            }

            .languages{
                margin: 10px auto;

                svg{
                    margin: 0;
                }
            }
        }
    }
}
