@import "../../index.scss";

.home {
    background-color: rgba($color: $dark, $alpha: 0.3);

    &::before {
        content: "";
        width: 100vw;
        height: 100vh;
        position: fixed;
        top: 0;
        background-image: url(./assets/bi.webp);
        background-repeat: no-repeat;
        background-size: 100% auto;
        background-position: 0 -1300px;
        z-index: -1;
    }

    header {
        height: 50vh;
        display: flex;
        flex-direction: column;
        justify-content: center;

        .timer {
            margin: 0 auto;
            display: flex;
            text-align: center;

            .column {
                width: 150px;
                padding: 0 30px;

                &:nth-of-type(2) {
                    border-left: 2px solid $light;
                    border-right: 2px solid $light;
                }
                &:nth-of-type(3) {
                    border-right: 2px solid $light;
                }

                p {
                    font-weight: 300;
                }
            }
        }

        h1 {
            margin: 50px 0 0 0;
            text-align: center;
            text-transform: uppercase;
            font-weight: 500;
        }
    }

    main {
        background-color: $dark;

        section {
            h2 {
                margin: 50px 0;
                text-align: center;
            }
        }

        #contactus {
            form {
                min-width: 320px;
                max-width: 600px;
                margin: auto;
                padding: 20px;

                input,
                textarea,
                button {
                    margin: 5px 0;
                }

                input,
                textarea {
                    width: 100%;
                    padding: 10px;
                    background-color: transparent;
                    border: 1px dashed rgba($color: $light, $alpha: 0.8);
                    outline: none;

                    &::placeholder {
                        color: $light;
                    }

                    &:hover,
                    &:focus {
                        box-shadow: 0 0 10px $light;
                    }
                }

                textarea {
                    min-height: 80px;
                    max-height: 200px;
                    resize: vertical;
                }

                button {
                    width: 100%;
                }
            }
        }

        #aboutus {
            .row {
                margin: 30px 0;

                svg {
                    width: 40px;
                    margin: 0 20px 0 0;
                    fill: $secondary;
                }

                h4 {
                    font-weight: 500;
                    text-transform: uppercase;
                }
            }
        }

        #services {
            .block {
                width: calc(100% / 3);
                padding: 10px;
                text-align: center;
                border: solid 1px $light;

                & > div {
                    height: 100%;
                    padding: 50px 30px;
                    display: flex;
                    flex-direction: column;
                    justify-content: space-between;
                    background: radial-gradient(circle at 0 0, rgb(34, 76, 215), rgba(255, 0, 0, 0) 70%),
                        radial-gradient(circle at 100% 50%, rgb(227, 66, 83), rgba(0, 0, 255, 0) 60%),
                        radial-gradient(circle at 20% 90%, rgb(254, 182, 86), rgba(0, 255, 0, 0) 90%) rgb(208, 0, 255);
                }

                p {
                    margin: 10px 0;
                }

                h3 {
                    padding: 20px 0;
                    border-top: 1px solid $light;
                    border-bottom: 1px solid $light;

                    s {
                        margin: 0 0 0 5px;
                        font-size: 50%;
                        opacity: 0.8;
                    }
                }

                button {
                    margin: 20px 0 0 0;
                }
            }
        }
    }

    @media only screen and(max-width: 1200px) {
        &::before {
            background-position: 0 -300px;
        }
    }

    @media only screen and(max-width: 768px) {
        &::before {
            background-position: 0 0;
        }

        header {
            .timer {
                .column {
                    width: 100px;
                    padding: 0 10px;
                }
            }
        }

        main {
            #services {
                .row {
                    display: grid;
                    grid-template-areas:
                        "express express"
                        "standard accelerated";

                    .block {
                        width: 100%;

                        &:nth-of-type(1) {
                            grid-area: standard;
                        }
                        &:nth-of-type(2) {
                            grid-area: accelerated;
                        }
                        &:nth-of-type(3) {
                            grid-area: express;
                        }
                    }
                }
            }
        }
    }

    @media only screen and(max-width: 500px) {
        header {
            height: max-content;

            .timer {
                .column {
                    width: 70px;
                    padding: 0 10px;
                }
            }
        }

        main {
            #services {
                .row {
                    grid-template-areas:
                        "express"
                        "accelerated"
                        "standard";
                }
            }
        }
    }
}
